import { View, Text, Image, FlatList } from 'react-native'
import orders from '../../../assets/data/orders.json'
import styles from '../RestaurantDetailsScreen/styles';
import OrderListItem from '../../components/OrderListItem';


import restaurant from "../../../assets/data/restaurants.json"
import BasketDishItem from '../../components/BasketDishItem';


const order = orders[0];
const OrderDetialsHeader = () => {

  return (
    <View>
    <View style={styles.page}>
      <Image
        source={{ uri: order.Restaurant.image }}
        style={styles.image}
        resizeMode="cover"
      />
      <View style={styles.container}>
        <Text style={styles.title}> {order.Restaurant.name} </Text>
        <Text style={styles.subtitle}> {order.status} - 2 days ago</Text>          
       
        <Text style={styles.menuTitle}> Your orders </Text>


      </View>
    </View>
    </View>
  )
}
const OrderDetials = () => {
    return(
        <FlatList
        ListHeaderComponent={OrderDetialsHeader}
        data={restaurant[0].dishes}
        renderItem={({ item }) => <BasketDishItem basketDish={item} />}
      />
    )
}


export default OrderDetials